.movie_genre_main {
    position: relative;
}
.movie_genre_slide {
    position: relative;
    width: 90%;
    overflow: hidden;
    left: calc(50%-825px);
}
.movie_genre_slide_show {
    padding: 100px 0 0 0;
    display: flex;
    transition: all 0.4s ease;
}
.left,
.right {
    background-color: transparent;
    border: none;
    outline: none;
    opacity: 0.8;
    font-size: 44px;
    transition: all 0.4s;
}
.left:hover,
.right:hover {
    cursor: pointer;
    transform: scale(1.1);
}

.left {
    text-shadow: 3px 2px 0 #009efa;
    position: absolute;
    top: calc(50% -22px);
    left: 80px;
}

.right {
    text-shadow: -3px 2px 0 #009efa;
    position: absolute;
    top: calc(50%-22px);
    right: 80px;
}
