@font-face {
    font-family: 'SUITE-Regular';
    src: url('https://cdn.jsdelivr.net/gh/projectnoonnu/noonfonts_2304-2@1.0/SUITE-Regular.woff2')
        format('woff2');
    font-weight: 400;
    font-style: normal;
}
* {
    box-sizing: border-box;
}
body {
    padding: 0;
    margin: 0;
    background-color: #485461;
    background-image: linear-gradient(100deg, #485461 0%, #28313b 74%);
    height: 100%;
}
.movies {
    position: fixed;
    width: 100vw;
    top: 150px;
    left: 40px;
    height: calc(100vh -600px);
    perspective: 1300px;

    backface-visibility: hidden;
}
.movies_wrap {
    display: flex;
    align-items: center;
    width: 300px;
    height: 470px;
    position: absolute;
    top: 5%;
    left: 45%;
    transform: translate(-40%);
    transform-style: preserve-3d;
    animation: ani linear 30s infinite;
}
.movies a {
    text-decoration: none;
    color: #a89c9c;
    text-align: center;
    font-family: 'SUITE-Regular';
}
.movies_wrap > :nth-child(1) {
    transform: rotateY(0deg) translateZ(-955px);
}

.movies_wrap > :nth-child(2) {
    transform: rotateY(30deg) translateZ(-955px);
}

.movies_wrap > :nth-child(3) {
    transform: rotateY(60deg) translateZ(-955px);
}

.movies_wrap > :nth-child(4) {
    transform: rotateY(90deg) translateZ(-955px);
}

.movies_wrap > :nth-child(5) {
    transform: rotateY(120deg) translateZ(-955px);
}

.movies_wrap > :nth-child(6) {
    transform: rotateY(150deg) translateZ(-955px);
}

.movies_wrap > :nth-child(7) {
    transform: rotateY(180deg) translateZ(-955px);
}
.movies_wrap > :nth-child(8) {
    transform: rotateY(210deg) translateZ(-955px);
}
.movies_wrap > :nth-child(9) {
    transform: rotateY(240deg) translateZ(-955px);
}

.movies_wrap > :nth-child(10) {
    transform: rotateY(270deg) translateZ(-955px);
}
.movies_wrap > :nth-child(11) {
    transform: rotateY(300deg) translateZ(-955px);
}
.movies_wrap > :nth-child(12) {
    transform: rotateY(330deg) translateZ(-955px);
}

@keyframes ani {
    0% {
        transform: rotateY(0deg);
    }
    100% {
        transform: rotateY(360deg);
    }
}
